.loader-lines {
  perspective: 1000px;
  width: 300px;
  height: 300px;
  color: #fff;
  border-radius: 50%;
  position: fixed;
  top: calc(50vh - 150px);
  left: calc(50vw - 150px);
  transform: rotateZ(45deg);
}

.loader-lines:before, .loader-lines:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader-lines:after {
  color: #ff3d00;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .4em 0;
  }

  12% {
    box-shadow: .4em .4em;
  }

  25% {
    box-shadow: 0 .4em;
  }

  37% {
    box-shadow: -.4em .4em;
  }

  50% {
    box-shadow: -.4em 0;
  }

  62% {
    box-shadow: -.4em -.4em;
  }

  75% {
    box-shadow: 0 -.4em;
  }

  87% {
    box-shadow: .4em -.4em;
  }
}

